<template>
    <div class="batch-deal">
      <header>
        <div class="logo-text">
          <i class="iconfont icon-logo"></i>
          <span>|&nbsp;&nbsp;智能设计</span>
        </div>
        <span class="login-btn"><i class="iconfont icon-yonghu"></i>{{this.loginUserData}}</span>
      </header>
      <div class="content">
        <div class="deal-btn-box">
          <Button class="common-btn">最新上传</Button>
          <Button class="history">历史记录</Button>
          <Button class="common-btn fr" @click="clickUpload">批量上传图片</Button>
          <span class="fr remind-text"><i class="iconfont icon-iconset0144"></i>图片文件要求：分辨率大于800X800,小于3000X3000；尺寸小于7MB</span>
          <input type="file" ref="uploadImg"  multiple="multiple" @change="uploadImg" style="display: none"
                 accept="image/jpeg,image/jpg,image/png">
        </div>
        <Table border ref="selection" :columns="columns4" :data="data1"></Table>
        <Page :total="100" />
        <div class="settings">
          <div class="bg">
            <div>背景：</div>
            <RadioGroup v-model="bg" vertical>
              <Radio label="transparent">
                <span>透明背景</span>
              </Radio>
              <Radio label="white">
                <span>纯白背景</span>
              </Radio>
            </RadioGroup>
          </div>
          <div class="size">
            <div>尺寸：</div>
            <RadioGroup v-model="size" vertical>
              <Radio label="small">
                <span>800X800</span>
              </Radio>
              <Radio label="big">
                <span>1200X1200</span>
              </Radio>
              <Radio label="origin">
                <span>原图尺寸</span>
              </Radio>
            </RadioGroup>
          </div>
          <Button class="common-btn">开始抠图</Button>
          <Button class="common-btn result">导出结果图</Button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "batchDeal",
        data(){
            return{
                loginUserData: '',
                columns4: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '文件名',
                        key: 'name'
                    },
                    {
                        title: '原始尺寸',
                        key: 'size'
                    },
                    {
                        title: '状态',
                        key: 'status'
                    }
                ],
                data1: [
                    {
                        name: 'John Brown',
                        age: 18,
                        address: 'New York No. 1 Lake Park',
                        date: '2016-10-03'
                    }
                    ],
                bg: 'transparent',
                size: 'small',
                files: [],
                obj: {}
            }
        },
        methods: {
            clickUpload(e) {
                this.$refs.uploadImg.click()
            },
            uploadImg(e) {
                this.files = e.target.files
                this.getImageSize(this.files[0])
            },
            getImageSize(file) {
                let self = this
                let reader = new FileReader()
                reader.readAsDataURL(file)
                reader.onload = function(theFile){
                    let img = new Image()
                    img.src = theFile.target.result
                    img.onload = function(){
                        self.obj={
                            fileWidth: this.width,
                            fileHeight: this.height
                        }
                    }
                }
            }
        },
        mounted() {
            this.loginUserData = sessionStorage.getItem('username') || ''
        },
        watch: {
            obj() {
                console.log(8888, this.obj)
            }
        }
    }
</script>

<style scoped lang="less">
.batch-deal{
  header {
    width: 100%;
    height: 70px;
    background: rgba(248, 248, 248, 1);
    opacity: 0.96;
    padding: 0 15%;
    .logo-text {
      font-size: 20px;
      color: #555;
      display: inline-block;
      .icon-logo {
        font-size: 50px;
        color: #555;
      }
      span {
        transform: translateY(-15px);
        display: inline-block;
      }
    }
    .login-btn {
      float: right;
      /*width: 80px;*/
      margin-top: 25px;
      color: #555;
    }
  }
  .content{
    width: 70%;
    margin: 0 auto;
    .deal-btn-box{
      margin: 30px 0 20px;
      .history{
        margin-left: 10px;
        color: #999;
      }
      .remind-text{
        padding: 1px;
        margin-right: 20px;
        .iconfont{
          transform: translateY(2px);
          display: inline-block;
          padding-right: 4px;
        }
      }
    }
    .ivu-page{
      float: right;
      margin: 20px 0;
    }
    .settings{
      clear: both;
    }
    .bg, .size{
      float: left;
      margin-right: 50px;
      >div:first-child{
        float: left;
        padding: 5px 10px 0 0;
      }
    }
    .result{
      float: right;
    }
  }
}
</style>



